	module.exports = {"data":{"wordpressPost":{"title":"Prenatal exposure to cannabis, overdiagnosis, and counterfeit medicines: Most read articles in April","content":"<h1>Prenatal exposure to cannabis, public definitions of overdiagnosis, and counterfeit medicines in Peru</h1>\n<p><img class=\"alignnone\" title=\"Image: Swanger vrou2.jpg, from Flickr.com, by Swangerschaft\" src=\"https://upload.wikimedia.org/wikipedia/commons/7/7c/Swanger_vrou2.jpg\" alt=\"File:Swanger vrou2.jpg\" width=\"241\" height=\"322\" /></p>\n<p>The Top 10 Most Read papers list this month contains a number of new entries, many of which have been published within the current issue. They come from a wide range of of areas and some have attracted attention via press release, blogs and discussion online. In the top spot is a systematic review and meta-analysis by Gunn <em>et al </em>which was published with an accompanying <a href=\"http://blogs.bmj.com/bmjopen/2016/04/05/cannabis-use-in-pregnancy-linked-to-low-birthweight-and-intensive-care/\">press release</a> at the start of the month, assessing the effects of use of cannabis during pregnancy on maternal and fetal outcomes. In at numbers 3 and 5 respectively are Medina <em>et al</em> with a retrospective review on drug alerts related to counterfeit medicines in Peru; and Hirono <em>et al</em>, who explore the usefulness of health impact assessment in trade negotiations. Complete with an <a href=\"http://blogs.bmj.com/bmjopen/2016/04/28/what-is-overdiagnosis-public-meanings-and-misconceptions/\">interview</a> with the lead author on the <a href=\"http://blogs.bmj.com/bmjopen/\"><em>BMJ Open</em> blog</a>, Ghanouni <em>et al</em> enter the list at number 8 with their survey of public definitions of the term &#8216;overdiagnosis&#8217; in the UK, and Esch <em>et al</em> examine patient engagement through open notes at number nine. Making it in at number ten this month is a cross-sectional study on polypharmacy by O&#8217;Dwyer <em>et al</em>, which evaluates polypharmacy in older people with intellectual disability, compared with the general population.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Gunn<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/4/e009986.full\">Prenatal exposure to cannabis and maternal and child health outcomes: a systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Kristensen <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/9/e007118.full\">The effect of statins on average survival in randomised trials, an analysis of end point postponement</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Medina <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/4/e010387.full\">Counterfeit medicines in Peru: a retrospective review (1991-2014)</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Martinez Steele<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/3/e009892.full\">Ultra-processed foods and added sugars in the US diet: evidence from a nationally representative cross-sectional study</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Hirono <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/4/e010339.full\">Is health impact assessment useful in the context of trade negotiations? A case study of the Trans Pacific Partnership Agreement</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Teschke <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/11/e008052.full\">Bicycling injury hospitalisation rates in Canadian jurisdictions: analyses examining associations with helmet legislation and mode share</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Boulton et<em> al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/3/e010330.full\">How much sugar is hidden in drinks marketed to children? A survey of fruit juices, juice drinks and smoothies</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Ghanouni<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/4/e010723.full\">Survey of public definitions of the term &#8216;overdiagnosis&#8217; in the UK</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Esch<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e010034.full\">Engaging patients through open notes: an evaluation using mixed methods</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">O&#8217;Dwyer <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/4/e010505.full\">Factors associated with polypharmacy and excessive polypharmacy in older people with intellectual disability differ from the general population: a cross-sectional observational nationwide study</a></td>\n</tr>\n</tbody>\n</table>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"a90ad787-e8e0-5142-b92a-5c9f5c9490a7"}}

